@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&family=Fredericka+the+Great&display=swap);
body {
  margin: 0;
  font-family: 'Amatic SC', cursive;
    letter-spacing: 3px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /* Ex app-header style*/
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

hr.style2 {
  overflow: visible; /* For IE */
  height: 30px;
  border-style: solid;
  border-color: white;
  border-width: 1px 0 0 0;
  border-radius: 20px;
}
hr.style2:before { /* Not really supposed to work, but does */
  display: block;
  content: "";
  height: 30px;
  margin-top: -31px;
  border-style: solid;
  border-color: white;
  border-width: 0 0 1px 0;
  border-radius: 20px;
}

.title {
  font-family: 'Amatic SC', cursive;
  letter-spacing: 2px;
  font-size: 4em;
  padding-bottom: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  font-family : inherit;
  font-size: 1em;
  color: whitesmoke;
  margin: 5px;
  padding-left: 10px;
  padding-right: 10px;  border-radius: 5px;
  background: transparent; 
}
button:hover {
  color: black;
  margin: 5px;
  background: whitesmoke; 
  cursor: pointer;
}


